.element__heart {
  background-image: url(../../../images/Heart.svg);
  background-repeat: no-repeat;
  border: none;
  background-color: #FFFFFF;
  width: 22px;
  height: 19px;
  flex-shrink: 0;
  cursor: pointer;
  transition: opacity 0.3s linear;
}

.element__heart:hover {
  opacity: 0.6;
}
