.elements {
  max-width: 880px;
  position: relative;
  display: grid;
  grid-template-columns: repeat(auto-fit, 282px);
  row-gap: 20px;
  column-gap: 17px;
  margin: 51px auto 0;
  padding: 0;
  justify-content: start;
}

@media (max-width: 880px) {
  .elements {
    margin: 36px auto 0;
    justify-content: center;
  }
}
